@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
.box {
  width: 400px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 25vw;
  background: #343a40;
  text-align: center;
  transition: 0.25s;
  margin-top: 100px;
  border-radius: 15px;
}

#basic-nav-dropdown {
  color: #fff;
}

.box input[type="email"],
.box input[type="password"] {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 1px solid #17a2b8;
  padding: 10px 10px;
  outline: none;
  color: #17a2b8;
  border-radius: 24px;
  transition: 0.25s;
  width: 250px;
}

.box h1 {
  color: white;
  text-transform: uppercase;
  font-weight: 500
}

.box input[type="email"]:focus,
.box input[type="password"]:focus {
  width: 300px;
  box-shadow: 0 0 5px #17a2b8;
}

.box input[type="email"]:hover,
.box input[type="password"]:hover {
  box-shadow: 0 0 5px #17a2b8;
}

#new-email, #new-password {
  display: none;
}

.submit {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 1px solid #17a2b8;
  padding: 14px 40px;
  outline: none;
  color: #17a2b8;
  border-radius: 15px;
  transition: 0.25s;
  cursor: pointer
}

.submit:hover {
  background: #17a2b8;
  color: #fff;
}

#update, #delete, #cancel {
  display: inline;
  width: 100px;
  height: 50px;
  padding: 0;
  margin: 15px 3px 5px 3px;
}

#update:hover {
  background-color: #0062cc;
}

#delete:hover {
  background-color: #bd2130;
}

#cancel:hover {
  background-color: #1e7e34;
}

.text {
  color: #fff;
}

@media (max-width: 400px) {
  .box {
    left: -1% !important;
    width: 350px;
  }

  .nav-item {
    margin: 0 !important;
  }
}

@media (max-width: 800px) {
  .box {
    left: -2%;
  }

  .nav-item {
    margin-right: 25px;
  }

  .button {
    margin: 0 5px;
  }
}

@media (max-height: 700px) {
  .box {
    margin-top: 50px;
  }
}
.home {
  position: relative;
  height: 55vh;
}

/* Search Bar */
#search {
  width: 95%;
  display: flex;
  margin-top: 25px;
  margin-left: 2.5vw;
  justify-content: center;
}

#none {
  display: none;
}

.search-before {
  position: absolute;
  top: 50%;
}

.input:focus,
.input:hover,
.fields:focus,
.fields:hover {
  outline: none;
  box-shadow: 0 0 5px #000;
}

.input {
  width: 35vw;
  height: 45px;
  border-right: none;
  padding-left: 15px;
  border-radius: 5px 0 0 5px;
}

.input:focus {
  width: 36.5vw;
  border-color: rgba(0, 0, 0, 0.6) !important;
}

.fields {
  width: 80px;
  height: 45px;
  border-left: none;
  text-align: center;
  background-color: #e2dcc552;
  border-radius: 0 5px 5px 0;
}

.search-button {
  height: 44px;
  background-color: #343a40;
  width: 100px;
}

.search-button:hover {
  background-color: #17a2b8;
  cursor: inherit !important;
}

.search-button:focus {
  outline: none;
}

@media (max-width: 400px) {
  .search-before {
    margin-left: 7px;
  }

  .input {
    width: 50vw;
  }

  .search-button {
    width: 70px;
    margin-right: 0;
  }

  .fields {
    width: 55px;
  }
}

/* Search Results */
.search-results {
  width: 90%;
  margin: 15px auto;
}

.book-list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  justify-content: center;
}

@media (max-width: 350px) {
  .search-results {
    width: 100%;
  }

  .book-list {
    padding: 15px;
  }
}

/* Book Result div */
.book {
  width: 250px;
  height: 450px;
  margin: 15px 20px;
  padding-top: 15px;
  border-radius: 5px;
  background-color: #f9f9f9;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.book-cover {
  margin: 10px auto;
  text-align: center;
  overflow: hidden;
  width: 200px;
  height: 290px;
}

.book-title, .book-author {
  margin: 0;
  overflow: hidden;
}

.book-title {
  height: 60px;
  font-weight: 700;
  font-size: 1.1rem;
}

.book-author {
  height: 30px;
}

.add-button {
  margin: 0;
  width: 100%;
  height: 35px;
  border: none;
  padding: 0;
  color: #fff;
  border-radius: 0 0 5px 5px;
  background-color: rgb(3, 118, 184);
}

.published-year {
  font-size: 0.8em;
}

@media (max-width: 350px) {
  .navbar {
    width: 100%;
    padding: 15px 5px;
  }
}

.site-title {
  font-family: 'Fredericka the Great';
  font-size: 35pt;
  color: #e1dcc5 !important;
}

.site-title:focus,
.site-title:hover,
.nav-link:focus,
.nav-link:hover {
  outline: none !important;
  text-decoration: underline;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e1dcc5;
  height: 100%;
  text-align: center;
  font-size: 13pt;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

#msg {
  color: rgb(250, 55, 55);
  display: none;
  font-size: 11pt;
}

